/* header section */
header {
  height: 5.6rem;
  background-color: #2874f0;
}
nav {
  max-width: 124.8rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: flex;
}
.searchNav {
  /* justify-content: flex-start; */
  margin-left: 3rem;
  min-width: 3.4rem;
  width: calc(100% - 170px);
  justify-content: flex-start;
}

.searchNav input {
  width: 55rem;
  height: 3.8rem;
  font-family: Roboto, Arial, sans-serif;
  letter-spacing: 0;
  padding: 0 1.6rem;
  border-radius: 0.2rem 0 0 0.2rem;
  border: 0;
  outline: 0 none;
  font-size: 1.4rem;
  margin-left: 1.6rem;
}
.searchBar {
  color: #2874f0;
  font-family: 20px;
  background: #fff;

  padding: 0.8rem;
  position: relative;
  cursor: pointer;
  z-index: 1;
  border: 0.1rem solid #fff;
  /* background-color: #fff; */
  height: 3.8rem;
  /* width: 4.5rem; */
  border: 0;
  border-radius: 0.2rem;
  outline: 0 none;

  /* color: #2874f0; */
}
.searchBox {
  display: flex;
}
.searchBar .sBarIcon ::after {
  position: absolute;
  height: 100%;
  width: 100%;
  content: "";
  background-color: red;
  top: 0;
  left: 0;
}
.searchBar .sBarIcon {
  font-size: 3rem;
  position: relative;
  top: -4px;
}
.searchBar .sBarIcon:hover {
  height: 3rem;
  transition: height 0.1s;
}
nav a {
  text-decoration: none;
}

.logoL {
  font-size: 1.1rem;
  font-style: italic;
  margin-top: 2.8rem;
  position: absolute;
  color: #f0f0f0;
  padding-left: 0.4rem;
}
.logoL span {
  color: #ffe500;
  font-weight: 500;
  margin-right: 0.2rem;
}
nav .logo {
  text-decoration: none;
  color: inherit;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  min-width: 13.4rem;
  margin-left: 4rem;
}
.navLogin {
  display: flex;
  align-items: center;
}
.loginBtn {
  color: #2874f0;
  font-weight: 500;
  background-color: #fff;
  cursor: pointer;
  border-radius: 0.2rem;
  height: 3.2rem;
  padding: 0.5rem 3.7rem;
  border: 1px solid #dbdbdb;
  font-family: Roboto, Arial, sans-serif;
  letter-spacing: 0;
  font-size: 1.8rem;
  margin: 0 2rem;
}
.navLinks {
  color: #fff;
  font-size: 1.6rem;
  font-weight: 500;
  font-family: Roboto, Arial, sans-serif;
  letter-spacing: 0;
  padding-left: 2rem;
}
.moreBtn {
  display: flex;
}
.mDownArrow {
  color: #fff;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  color: #fff;
  margin-left: 0.3rem;
  position: relative;
  top: 0.3rem;
}
.mDownArrow:hover {
  -webkit-transform: rotate(0.5turn);
          transform: rotate(0.5turn);
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}
.sCart {
  margin-right: 0.5rem;
}
.burger {
  position: absolute;
  left: 2rem;
  top: 2rem;
  display: none;
  
}
.burger .toggleMe {
  display: block;

  border: none;
  padding: 0;
  /* z-index: 200; */
}


.cutIcon .clearIcon {
  color: #fff;
  font-size: 3.3rem;
  
}
.cutIcon {
  position: absolute;
  right: 40%;
  top: 2rem;
  display: none;
  

  cursor: pointer;
  z-index: 300;
}


.line {
  width: 3.1rem;
  background-color: #fff;
  height: 0.23rem;
  margin: 0.57rem 0.3rem;
}
.clearIcon {
  position: absolute;
  top: 0;
  left: 0;
  color: rgb(243, 243, 243);
  font-size: 3.3rem;
}


.respHamburger {
  background-color: rgb(243, 243, 243);
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 90px, rgba(0, 0, 0, 0.07) 0px 90px 8px, rgba(0, 0, 0, 0.07) 0px 90px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
  height: 100vh;
  margin: 0;
  position: absolute;
  top:0;
  z-index: 100;
  width: 60%;
  display: none;
  -webkit-animation: animateNav .2s ease-in-out;
          animation: animateNav .2s ease-in-out;
  /* transition: width .2s ease-in; */
}
.animate{
  -webkit-animation: animateNav .3s ease-in-out 1 infinite;
          animation: animateNav .3s ease-in-out 1 infinite;
}



.respHamburger2 {
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0.3;
  height: 100vh;
