}
.sSlider .slick-arrow {
  background-color: red;
}
.sSlider .slick-dots{
  bottom: 10px;
}
.sSlider .slick-dots li{
  margin: 0;
}

.sSlider .slick-next {
  right: 0px;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  background-color: hsla(0, 0%, 100%, 0.98);
  width: 48px;
  height: 83px;
  z-index: 22;
  fill: black;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 11%);
}
.sSlider .slick-next svg,.sSlider .slick-prev svg{
  color: #000;
  font-size: 4rem;
  top: 19px;
  position: relative;
}
.sSlider .slick-next:hover,
.sSlider .slick-prev:hover {
  background-color: hsla(0, 23%, 90%, 0.98);
}
 .sSlider .slick-prev:before ,.sSlider .slick-next:before{
  content: ""!important;
}

.sSlider .slick-prev {
  left: 0;
  fill: black;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 11%);

  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  background: hsla(0, 0%, 100%, 0.98);
  position: absolute;
  width: 48px;
  height: 83px;
  z-index: 220;
}

