/* deal conatainer */
.dealSection {
  margin: 1.6rem 1rem 2rem 1rem;
}
.dContainer {
  display: grid;
  background-color: #fff;
  margin: 0 auto;
  min-width: 978px;
  max-width: 1680px;
  overflow: hidden;
  grid-gap: 4px 10px;
  grid-template-columns: 5fr 1fr;
  grid-template-rows: 60px 250px;
  flex-grow: auto;

  grid-template-areas:
    "i1 i2"
    "i3 i2";
}
.i1 {
  grid-area: i1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.i1Style {
  display: flex;
  align-items: center;
  padding-left: 3rem;
}
.i1 button {
  background-color: #2874f0;
  color: #fff;
  margin-right: 2rem;
  height: 35px;
  border: none;
  outline: none;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 20%);
  border-radius: 2px;
  width: 100px;
  font-size: 13px;
  font-weight: 500;
  padding-top: 7px;
  transition: box-shadow 0.2s ease;
  font-family: Roboto, Arial, sans-serif;
}
.i1 button:hover {
  background-color: #2874f0;
}
.i1Style img {
  width: 25px;
  height: 25px;
}
.i1Style span {
  font-size: 18px;
  padding-left: 2rem;
  opacity: 0.5;
  text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3),
    0px -4px 10px rgba(255, 255, 255, 0.3);
}
.i1Style h1 {
  padding-right: 3rem;
  font-size: 22px;
  line-height: 32px;
  display: inline-block;
  font-weight: 500;
}
.i2 {
  grid-area: i2;
}
.dContainer .i2 img {
  object-fit: contain;
  border-radius: 8px;
  
}

.dContainer .i2 img:hover {
  cursor: pointer;
}

.i3 {
  grid-area: i3;
}

.i3 .dC {
  display: inline-block;
  padding: 24px 15px;
