    outline: none;
}
.txt-field label{
    position: absolute;
    top: 50%;
    left: 5px;
    color: #adadad;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 1.6rem;
    pointer-events: none;
    transition: .3s;
