    display: inline-block;
    position: absolute;
    bottom: 10px;
    right: 0;
    font-weight: 500;
    font-size: 13px;
    cursor: pointer;
    color: #2874f0;
    text-decoration: none;
}
.txt-field a:hover{
    text-decoration: underline;
}
.txt-field span::before{
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    width:0;
    height: 1px;
    background: #2691d9;
    transition: .3s;


}
.txt-field input:focus ~ label,
.txt-field input:valid ~ label{
    top: -5px;
    color: #2691d9;
    font-size: 12px;
}
.txt-field input:focus ~ span::before,
.txt-field input:valid ~ span::before{
    width: 100%;
}
